	module.exports = {"data":{"postsJson":{"title":"Be Reconciled!","questionsTitle":"DIGGING DEEPER","display":"block","bookName":"Philemon","ref":"1:1-1:25","html":"<p>   <strong>1</strong><u>1</u>Paul, a prisoner of Christ Jesus, and Timothy our brother, to Philemon, our beloved fellow worker, <u>2</u>to the beloved Apphia, to Archippus, our fellow soldier, and to the assembly in your house: <u>3</u>Grace to you and peace from God our Father and the Lord Jesus Christ.<br />   <u>4</u>I thank my God always, making mention of you in my prayers, <u>5</u>hearing of your love, and of the faith which you have toward the Lord Jesus, and toward all the saints; <u>6</u>that the fellowship of your faith may become effective, in the knowledge of every good thing which is in us in Christ Jesus. <u>7</u>For we have much joy and comfort in your love, because the hearts of the saints have been refreshed through you, brother.<br />   <u>8</u>Therefore though I have all boldness in Christ to command you that which is appropriate, <u>9</u>yet for love’s sake I rather beg, being such a one as Paul, the aged, but also a prisoner of Jesus Christ. <u>10</u>I beg you for my child, whom I have become the father of in my chains, Onesimus,* <u>11</u>who once was useless to you, but now is useful to you and to me. <u>12</u>I am sending him back. Therefore receive him, that is, my own heart, <u>13</u>whom I desired to keep with me, that on your behalf he might serve me in my chains for the Good News. <u>14</u>But I was willing to do nothing without your consent, that your goodness would not be as of necessity, but of free will. <u>15</u>For perhaps he was therefore separated from you for a while, that you would have him forever, <u>16</u>no longer as a slave, but more than a slave, a beloved brother, especially to me, but how much rather to you, both in the flesh and in the Lord.<br />   <u>17</u>If then you count me a partner, receive him as you would receive me. <u>18</u>But if he has wronged you at all, or owes you anything, put that to my account. <u>19</u>I, Paul, write this with my own hand: I will repay it (not to mention to you that you owe to me even your own self besides). <u>20</u>Yes, brother, let me have joy from you in the Lord. Refresh my heart in the Lord. <u>21</u>Having confidence in your obedience, I write to you, knowing that you will do even beyond what I say.<br />   <u>22</u>Also, prepare a guest room for me, for I hope that through your prayers I will be restored to you.<br />   <u>23</u>Epaphras, my fellow prisoner in Christ Jesus, greets you, <u>24</u>as do Mark, Aristarchus, Demas, and Luke, my fellow workers. <u>25</u>The grace of our Lord Jesus Christ be with your spirit. Amen.</p><h5>*Philemon 1:10 Onesimus means “useful”.</h5>","path":"/403","Question1":"Why is Paul writing to Philemon?","Question2":"Who was Onesimus and how did Paul meet him?","Question3":"How did Paul expect the gospel to reshape the relationship between Philemon and Onesimus?","Question4":"Are there accepted relationships in your culture that the gospel would reshape? What would that look like?","prevPath":"/402","nextPath":"/404"}},"pathContext":{}}

